@import '../../common/colors.scss';

.movie-list, .show-list{
    margin:20px 0px;


    h2{
        color:$font-secondary;
        margin-bottom: 20px;
        font-weight: 400;
    }

}


.movie-container, .show-container{
    border:1px solid black;
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(220px,1fr));
    grid-gap: 20px;
}
