@import '../../common/colors.scss';

.movie-section {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 40px 0px;
    font-size: 18px;
    color: $font-primary;
    font-weight:400;}


.movie-rating { 
padding-left: 3px;
margin-top: 30px;
color: $font-secondary;
display: flex;
flex-wrap: wrap;

span {
        margin-right:20px}
}

.movie-title {
    font-size: 30px;
    line-height: 2rem;
    color: $font-primary;
}

.movie-plot {
    margin-top: 30px;
    line-height: 1.8rem;
}

.movie-info{
    margin-top: 30px;
}
.movie-info>div span:first-child {
    padding: 10px 0px;
    color: $font-primary;
    font-weight: 600;
    width: 100px;
    display: inline-block;
}


.movie-info>div span {
    color:$font-secondary;
}

.section-right{
    margin-top: 30px;
}